* {
  font-family: sans-serif;
  font-size: 14px;
  margin: 0;
  padding: 0;
  border: 0;
  vertical-align: baseline;
}

.all {
  display: flex;
  flex-direction: column;
}

p {
  display: flex;
  justify-content: center;
  text-align: center;
}

b {
  text-align: center;
}

.zle_tlu {
  color: red;
}

.dobre_tlu {
  color: green;
}

.div_bez {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 30%;
}

.pierwszy_rzad, .drugi_rzad, .trzeci_rzad, .czwarty_rzad {
  display: flex;
  justify-content: space-around;
  align-items: center;
  height: 19vh;
}

.pierwszy_rzad {
  background-color: #FFDD5C;
}

.drugi_rzad {
  background-color: #76A665;
}

.trzeci_rzad {
  background-color: #227066;
}

.czwarty_rzad {
  background-color: #265961;
}

h2 {
  margin-top: 0;
  margin-bottom: 0;
  font-size: 2rem;
}

input {
  width: 8rem;
  margin-top: 1rem;
}

button {
  background-color: #214154;
  width: 7rem;
  height: 3rem;
  border-radius: 50px;
  border: 0;
  color: white;
}

.but_flex {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 2rem;
}
